  0% {
    transform: translateY(6px); }
  100% {
    transform: translateY(1px); } }

/*hover ToolTip*/
.left:hover {
  transform: translateX(-6px); }

.top:hover {
  transform: translateY(-6px); }

.bottom:hover {
  transform: translateY(6px); }

.right:hover {
  transform: translateX(6px); }

/*left*/
.left .tooltip {
  top: -20%;
  left: -170%; }

.left .tooltip::after {
  top: 40%;
  left: 90%;
  transform: rotate(-90deg); }

/*bottom*/
.bottom .tooltip {
  top: 115%;
